.container {
  display: grid;
  grid-template-columns: 16% 16% 16% 16% 16% 16%;
  grid-template-rows: 33% 33% 33%;
  grid-column-gap: 2%;
  width: 80%;
  margin: 0 auto;
  height: 75vh;
  overflow: hidden;
  margin-top: 5%;
  background: black;
  padding: 2% 6% 0 2%;
  border-radius: 12px;
  box-shadow: 0px 0px 25px white;
}

.shake {
  animation: shake .5s 0.0125s;
}

@keyframes shake {
  from,
  to {
    transform: translate3d(0,0,0);
  }
    12.5%,
    37.5%,
    62.5%,
    87.5% {
      transform: translate(-10px);
    }

    25%,
    50%,
    75% {
      transform: translate(10px);
    }
  }